html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background-color: var(--dark-bg);
  color: var(--dark-text2);
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  height: 1;
  width: 1;
  overflow: hidden;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

h2 {
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 1.5rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-gradient-start: hsl(37, 97%, 70%);
  --instagram-gradient-end: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --toggle-bg-gradient-start: hsl(210, 78%, 56%);
  --toggle-bg-gradient-end: hsl(146, 68%, 55%);
  --toggle: hsl(230, 22%, 74%);
  --dark-bg: hsl(230, 17%, 14%);
  --dark-top-bg: hsl(232, 19%, 15%);
  --dark-card-bg: hsl(228, 28%, 20%);
  --dark-card-hover: hsl(228, 25%, 27%);
  --dark-text1: hsl(228, 34%, 66%);
  --dark-text2: hsl(0, 0%, 100%);
  --light-bg: hsl(0, 0%, 100%);
  --light-top-bg: hsl(225, 100%, 98%);
  --light-card-bg: hsl(228, 28%, 20%);
  --light-text1: hsl(228, 12%, 44%);
  --light-text2: hsl(230, 17%, 14%);
}

:root {
  --font-inter: "Inter", sans-serif;
}

.container {
  max-width: 69.375rem;
  padding: 0 1.5625rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.cards {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.875rem;
}
@media (min-width: 40em) {
  .cards {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 71.875em) {
  .cards {
    grid-template-columns: repeat(4, 1fr);
  }
}

.card {
  background-color: var(--dark-card-bg);
  color: var(--dark-text1);
  text-align: center;
  padding: 1.5625rem;
  border-radius: 0.3125rem;
  border-top: 0.3125rem solid;
  transition: background 150ms ease-in-out;
  cursor: pointer;
}
.card:hover {
  background-color: var(--dark-card-hover);
}
.card--facebook {
  border-color: var(--facebook);
}
.card--twitter {
  border-color: var(--twitter);
}
.card--instagram {
  border-color: transparent;
  border-image: linear-gradient(255deg, var(--instagram-gradient-start) 0%, var(--instagram-gradient-end) 98.02%);
  border-image-slice: 1;
}
.card--youtube {
  border-color: var(--youtube);
}
.card__platform {
  display: flex;
  height: 1.25rem;
  justify-content: center;
  align-items: center;
  margin-bottom: 1.75rem;
}
.card__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
}
.card__icon {
  margin-right: 0.5rem;
}
.card__username {
  font-size: 0.75rem;
  font-weight: 700;
}
.card__followers {
  margin-bottom: 1.75rem;
}
.card__count {
  color: var(--dark-text2);
  line-height: 1;
  letter-spacing: -0.125rem;
  margin-bottom: 0.25rem;
  font-weight: 700;
}
.card__count--small {
  font-size: 2rem;
}
.card__count--big {
  font-size: 3.5rem;
}
.card__label {
  font-size: 0.75rem;
  font-weight: 400;
  text-transform: uppercase;
  letter-spacing: 0.25rem;
}
.card__change {
  font-size: 0.75rem;
  font-weight: 700;
  display: flex;
  justify-content: center;
  align-items: center;
}
.card__change--up {
  color: var(--limegreen);
}
.card__change--down {
  color: var(--brightred);
}
.card__number {
  margin-left: 0.25rem;
}

.header {
  margin-top: 2.25rem;
  display: flex;
  flex-wrap: wrap;
}
@media (min-width: 40em) {
  .header {
    justify-content: space-between;
    align-items: center;
  }
}
.header__title {
  width: 100%;
}
@media (min-width: 40em) {
  .header__title {
    width: auto;
  }
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--dark-text1);
}
.header__toggle {
  margin: auto;
  margin-top: 0.9375rem;
}
@media (min-width: 40em) {
  .header__toggle {
    margin: 0;
  }
}

.toggle {
  border: none;
  display: grid;
  grid-template-columns: 1fr 3rem;
  gap: 0.8125rem;
}
.toggle__wrapper {
  height: 1.5rem;
  position: relative;
}
.toggle input[type=radio] {
  opacity: 0;
  margin: 0 -0.125rem;
  height: 1.5rem;
  width: 1.25rem;
  z-index: 1;
  position: relative;
}
.toggle input[type=radio]:focus ~ .toggle__button {
  border: 2px solid white;
}
.toggle__background {
  display: block;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  background: linear-gradient(225deg, var(--toggle-bg-gradient-end) 0%, var(--toggle-bg-gradient-start) 98.02%);
  border-radius: 0.75rem;
  pointer-events: none;
}
.toggle__button {
  width: 1.125rem;
  height: 1.125rem;
  border-radius: 50%;
  background-color: var(--dark-bg);
  position: absolute;
  left: 0.1875rem;
  top: 0.1875rem;
  right: 100%;
  transition: all 0.2s ease-in-out;
}
.toggle #dark:checked ~ .toggle__button {
  left: 3px;
  right: calc(100% - 21px);
}
.toggle #light:checked ~ .toggle__button {
  right: 3px;
  left: calc(100% - 21px);
}
.toggle #system:checked ~ .toggle__button {
  right: auto;
  left: 50%;
  transform: translateX(-50%);
}
.toggle label {
  font-size: 0.875rem;
  line-height: 1.5rem;
}

.card-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, auto);
  justify-items: start;
  gap: 1.4375rem;
  border: 0;
}
.card-grid .card__count {
  margin-bottom: 0;
}
.card-grid .card__count, .card-grid .card__change {
  align-self: end;
}
.card-grid img, .card-grid .card__change {
  justify-self: end;
}